import React from "react";
import "./navbar.css";
import cartLogo from './cart-icons/icons8-shopping-cart.gif'
const NavBar = (props) => {
  return (
    <>
      <div className="container">
        <div className="row">
          <div className="col  mg">
            <h1 className="Name-cart">C A R T</h1>
          </div>
          <div className="col  new_property numberofitem">
            <div>
              <img
                className="cart-image-property"
                alt="cart-icon"
                src={cartLogo}
              />
            </div>
            <div className="total-item-cart-nb">
              <span className="total-item-counter">{props.totalItem}</span>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default NavBar;
