import React from "react";

import { Cart } from "./Cart";

import NavBar from "./NavBar";
import "./app.css"
import firebase from 'firebase';
  
import "firebase/firestore";   



class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      products: [],
    };
  }
/*
  componentDidMount() {
    firebase
      .firestore()
      .collection("products")
      .get()
      .then((Snapshot) => {
        console.log(Snapshot);
      });
  }
  */
  componentDidMount() {
  
    console.log("mount call kia hai");
    firebase
      .firestore()
      .collection("products")
      .onSnapshot((Snapshot) => {
       
        console.log(Snapshot);
        const products = Snapshot.docs.map((item) => {
          //console.log(item);
          const data = item.data();
          data['id'] = item.id;
          return data;
        })
        
        //console.log(product);

          
          this.setState({
            products: products,
          });



      });
     

  }

  handleIncreseQuantity = (item) => {
    //console.log(item);

    const { products } = this.state;
    
    let index = products.indexOf(item);
     /*
    products[index].quantity += 1;

    this.setState({
      products: products,
    });
    */
    
    const docRef =
      firebase.firestore().collection('products').doc(products[index].id);
    
    docRef.update({
        quantity:products[index].quantity++,
    })
     




  };
  handleDecreaseQuantity = (item) => {
    
    
    const { products } = this.state;
    const index = products.indexOf(item);

    if (products[index].quantity === 0) {
      console.log("why not decreasion" + products[index].quantity);
      return;
    }

    const docRef = firebase
      .firestore()
      .collection("products")
      .doc(products[index].id);

    docRef.update({
      quantity: products[index].quantity--,
    });
  };
  handleDeleteProduct = (id) => {
    
   const docRef = firebase
     .firestore()
     .collection("products")
     .doc(id);
    
    docRef.delete()
    
  };

  totalProducts = () => {
    let totalItem = 0;
    const { products } = this.state;

    for (let i = 0; i < products.length; i++) {
      let value = products[i].quantity;

      totalItem += value;
    }

    return totalItem;
  };

  totalProductsCost = () => {
    let totalCost = 0;
    const { products } = this.state;
    for (let i = 0; i < products.length; i++) {
           let value = products[i].quantity * products[i].cost;
           totalCost += value;
    }    
      return totalCost;
  };

  
  render() {
    
    //console.log(this.props);
    //console.log(this.totalProducts());
   
    console.log('hello render');
    
    const { products } = this.state;
    console.log(products);
      return (
        <>
          
          <NavBar totalItem={this.totalProducts()} />
          <Cart
            products={products}
            onIncreaseQuantity={this.handleIncreseQuantity}
            onDecreaseQuantity={this.handleDecreaseQuantity}
            onDeleteProduct={this.handleDeleteProduct}
          />
          <div className="total-Products-Cost">
            <h1>TOTAL COST: {this.totalProductsCost()}</h1>
          </div>
        </>
      );
    }
  
}

export default App;
