import React from "react";
import "./cart-items.css";
import cartItemDelete from './cart-icons/icons8-trash.gif';
import plusIcon from './cart-icons/icons8-plus.gif';
import minusIcon from './cart-icons/icons8-minus.gif'



const  CartItem =(props)=> {
 
  

  console.log('dubara hello bhai');
    //console.log(props);
    const { item } = props;
    
     
    //console.log(item);

   // console.log(item);
    return (
      <>
        <div className="cart-item">
          <div className="left-block">
            <img src={item.img} className="item-image-style" alt="item-img" />
          </div>
          <div className="right-block">
            <div className='upper-block'>
              <div className="items-fonts itemName">{item.productName}</div>
              <div className="items-fonts">Cost:&nbsp; {item.cost}</div>
              <div className="items-fonts">Qty: &nbsp; {item.quantity}</div>
            </div>
            <div className='lower-block'>
              <div className="cart-items-action">
                <img
                  onClick={() => props.onIncreaseQuantity(item)}
                  className="action-icons"
                  src={plusIcon}
                  alt="increase"
                />
                <img
                  onClick={() => props.onDecreaseQuantity(item)}
                  className="action-icons"
                  src={minusIcon}
                  alt="delete"
                />
                <img
                  onClick={() => props.onDeleteProduct(item.id)}
                  className="action-icons"
                  src={cartItemDelete}
                  alt="delete"
                />
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }


export default CartItem;
